@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
* {
  margin: 0;
  padding: 0;
  /* border: 1px solid red; */
}
html {
  font-size: 62.5%;
  font-family: 'Quicksand';
}
body {
  background-color: #333;
}
::-webkit-scrollbar { 
  display: none;
}
header {
    background-color: #111;
    height: 10rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1000;
}
.header-container {
    height: 10rem;
    width: 100%;
    max-width: 150rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 15rem;
    margin-top: .5rem;
}
.header-logo {
    position: absolute;
    left: 15rem;
    top: 2.8rem;
    color: #ba0c0c;
    font-family: 'Righteous';
    font-size: 3.5rem;
    letter-spacing: .1rem;
    text-shadow: .25rem .25rem .25rem black;
    cursor: pointer;
}
.header-logo-img {
    position: absolute;
    left: 10.25rem;
    top: 3rem;
    width: 4rem;
    height: 4rem;
    padding-right: 1rem;
}
.header-menu {
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
}
.header-link {
    color: #fff;
    text-decoration: none;
}
.header-item {
    padding: 1rem 2rem;
    font-size: 1.15rem;
    margin: 0 2.5rem;
    line-height: 4rem;
}
.header-item::after {
    content: "";
    display: block;
    height: .3rem;
    width: 0;
    background-color: transparent;
    transition: all .7s ease;
}
.header-item:hover::after {
    width: 100%;
    background-color: #d82020;
}
.header-item .active {
    color: #d82020;
}
.account-btn {
    position: absolute;
    right: 16.5rem;
    font-size: 3rem;
    top: 3rem;
}
.account-btn .active {
    color: #d82020;
}
@media (max-width: 1200px) {
    html {
        font-size: 50%;
    }
    .header-item {
        margin: 0 1rem;
    }
    .header-logo {
        left: 6.25rem;
        font-size: 2.75rem;
        top: 3.15rem;
    }
    .header-logo-img {
        left: 2.5rem;
        height: 3rem;
        width: 3rem;
        top: 3.5rem;
    }
    .account-btn {
        right: 5rem;
        font-size: 2.5rem;
        top: 3.5rem;
    }
}
@media (max-width: 900px) {
    html {
        font-size: 45%;
    }
    .header-container {
        margin-left: 15rem;
    }
    .header-item {
        margin: 0;
        padding: 1.25rem;
    }
    .header-logo {
        left: 5.65rem;
        font-size: 2.25rem;
        top: 3.7rem;
    }
    .header-logo-img {
        left: 2.5rem;
        height: 2.5rem;
        width: 2.5rem;
        top: 3.85rem;
    }
    .account-btn {
        right: 3.5rem;
        font-size: 2.25rem;
        top: 3.85rem;
    }
}
.home-page {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: radial-gradient(#252525, #000);
}
.home-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    z-index: 10;
}
.home-text-container {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 7.5rem;
    opacity: 0;
    z-index: 25;
    animation: fade-home-description 2s ease-in .5s forwards;
}
.home-header {
    color: white;
    margin-bottom: 1.25rem;
    font-size: 8rem;
    font-weight: 900;
    letter-spacing: .075rem;
    text-shadow: .35rem .35rem 1rem black;
}
.home-description {
    color: #aaa;
    font-size: 2rem;
    letter-spacing: .25rem;
    margin-bottom: 1rem;
    text-shadow: .15rem .15rem .5rem black;
}
@keyframes fade-home-description {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.home-button {
    margin-top: 2.5rem;
    width: 14.25rem;
    height: 5rem;
    border-radius: 4.5rem;
    font-size: 1.5rem;
    font-weight: 600;
    border: none;
    transition: all .25s ease;
    text-transform: uppercase;
    z-index: 100;
    color: #fff;
    background-color: #000;
    box-shadow: .35rem .35rem 1rem rgba(0, 0, 0, 0.6);
}
.home-button:hover {
    cursor: pointer;
    transform: scale(1.075);
    color: #000;
    background-color: #fff;
}
.floating-coins {
