.watchlist-container {
    min-height: 60vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}
.watchlist {
    margin: 3rem 0;
    border: .5rem solid #111;
    background: linear-gradient(0deg, rgba(25,25,25,.75) 25%, rgba(25,25,25,0.25) 50%, rgba(25,25,25,0.1) 75%, rgba(25,25,25,0.075) 100%);
    width: 80vw;
    text-align: center;
    border-radius: .5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    box-shadow: .5rem .5rem 1rem rgba(0, 0, 0, 0.4);
}
.watchlist-top {
    width: 80vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    padding: 3rem 0 1.75rem;
    margin-left: -2.5rem;
}
.watchlist-top-count-title {
    margin-left: 5.35rem;
    font-size: 2.5rem;
    text-shadow: .25rem .25rem .15rem rgba(0, 0, 0, 0.445);
    letter-spacing: -.05rem;
    color: #ddd;
    text-transform: uppercase;
    display: inline-flex;
}
.watchlist-top-count {
    font-family: sans-serif;
    font-weight: 700;
    text-shadow: none;
    font-size: 3rem;
    color: #fff;
    line-height: 2.75rem;
    padding-left: 2.15rem;
    text-shadow: .25rem .25rem .5rem rgba(0, 0, 0);
    padding-top: .15rem;
}
.watchlist-sort-container {
    padding: 1.25rem 0;
    border-radius: 5rem;
    margin-right: 2.5rem;
    margin-left: -3.25rem;
    width: 42vw;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    letter-spacing: -.05rem;
}
.watchlist-sort-title {
    font-size: 2.425rem;
    text-shadow: .25rem .25rem .15rem rgba(0, 0, 0, 0.445);
    color: #ddd;
    text-transform: uppercase;
    margin-left: 4rem;
    margin-right: -4rem;
}
.watchlist-sort-subContainer {
    width: 23.5vw;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
    margin-left: .85rem;
}
.watchlist-filter input {
    margin: 0;
    padding: 0;
    appearance: none;
}
.watchlist-sort-item {
    font-weight: 900;
    font-size: 1.5rem;
    color: #d82020;
    line-height: 3rem;
    display: inline-block;
    text-transform: uppercase;
    letter-spacing: none;
    text-shadow: .1rem .1rem .1rem rgba(0, 0, 0, 0.445);
    border: .1625rem solid #191919;
    border-radius: 1rem;
    padding: .25rem .75rem;
    width: 7.5rem;
    background-color: rgba(25,25,25,0.5);
    transition: all .5s ease;
}
.watchlist-sort-item:hover {
    border-color: #181818;
    background-color: #181818;
    cursor: pointer;
}
.watchlist-filter input:checked + .watchlist-sort-item {
    background-color: #050505;
    border-color: #050505;
}
.watchlist-sort-item span {
    padding-right: .35rem;
}
.watchlist-upDown {
    margin-left: -3.5rem;
}
.watchlist-upDownContainer {
    margin: -1.75rem 0;
}
.watchlist-upDownContainer input {
    margin: 0;
    padding: 0;
    appearance: none;
}
.watchlist-upDown-item {
    height: 1rem;
    font-size: 3.25rem;
    color: #000;
    opacity: .275;
    transition: all .25s ease;
}
.watchlist-upDown-item:hover {
    opacity: .425;
    cursor: pointer;
}
.watchlist-upDownContainer input:checked + .watchlist-upDown-item {
    opacity: 1;
}
.watchlist-contents {
    padding: 1rem 0 3.5rem;
}
.watchlist-toMarket-button {
    text-align: center;
    width: 12rem;
    height: 4.65rem;
    border-radius: 1rem;
    font-size: 1.5rem;
    font-weight: 700;
    border: .15rem solid #181818;
    transition: all .25s ease;
    text-transform: uppercase;
    margin-left: 10rem;
    z-index: 100;
    color: #fff;
    background-color: #111;
    box-shadow: .15rem .15rem .15rem rgba(0, 0, 0, 0.4);
}
.watchlist-toMarket-button:hover {
    cursor: pointer;
    color: #000;
    background-color: #fff;
}